.work-item {
  display: flex;
  justify-content: center;
  align-items: center;

  .side-text {
    max-width: 300px;
    margin: 2rem;

  }

  img {
    max-width: 520px;
    height: auto;
  }
}

.reverse {
  flex-direction: row-reverse;
}

@media #{$mq-900-down} {
  .work-item img {
    max-width: 420px;
  }
}

@media#{$mq-760-down} {
  .work-item {
    flex-direction: column;
    justify-content: center;

    .side-text {
      margin: 0;
      max-width: 420px;
    }

    img {
      width: 100%;
    }
  }
}