.info {
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    max-width: 400px;
    height: auto;
  }

  p {
    max-width: 385px;
    line-height: 2.2;
    margin: 2rem;
  }
}



.botton-filler {
  position: absolute;
  bottom: 100px;
  left: 0;
  z-index: -99;
}


@media #{$mq-760-down} {
  .info {
    flex-direction: column;
    justify-content: center;

    p {
      margin: 0 0 2rem;
      max-width: 420px;
    }

    img {
      width: 100%;
    }
  }



  .botton-filler {
    display: none;
  }
}